@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.person {
    width: 310px;
    text-align: center;
    margin-top: 100px;
}

.person h1 {
    color: #FFEB00;
    font-size: 35px;
}

.person>div {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 10px;
    width: 100%;
    font-size: 16px;

    color: white;
}

.person-details:nth-of-type(even) {
    background: linear-gradient(to right, #FFEB00 50%, #2d2f30 50%);
}

.person-details:nth-of-type(even)>p:nth-last-of-type(2) {
    color: #2d2f30;
}

.person-details:nth-of-type(odd) {
    background: linear-gradient(to right, #2d2f30 50%, #FFEB00 50%);
}

.person-details:nth-of-type(odd)>p:nth-last-of-type(1) {
    color: #2d2f30;
}

.person>button {
    display: inline-flex;
    justify-content: space-between;
    align-items: center;

    padding: 10px;
    margin: 20px auto;
    width: 80px;
    font-size: 16px;

    border: none;
    background: #2d2f30;
    color: #FFEB00;
    cursor: pointer;
}
.container {
    margin: 100px auto;
}

.search-input {
    width: 500px;
    padding: 16px 100px 16px 24px;
    border: 0;
    outline: 0;

    font-size: 16px;

    background: #2d2f30;
    color: #f2f2f2;
}

.search-input--no-data {
    border-radius: 25px;
}

.search-input--data {
    border-radius: 25px 25px 0px 0px;
}

.logo {
    text-align: center;
    margin-bottom: 35px;
}

.logo img {
    width: 200px;
}

.search {
    position: relative;
}

.search-action {
    position: absolute;
    top: 50%;
    right: 1.5%;
    transform: translateY(-50%);

    border-radius: 25px;

    display: flex;
    align-items: center;
    justify-content: center;

    background: #2d2f30;
}

.search-action-clear {
    display: grid;
    place-items: center;

    padding: 12px;
    font-size: 20px;

    color: #f2f2f2;

    cursor: pointer;
}

.search-action__loading {
    color: #FFEB00;
}

.search-action__icon {
    display: grid;
    place-items: center;

    font-size: 15px;
    padding: 12px;
    border-radius: 50%;

    background-color: #FFEB00;

    cursor: pointer;
}


@media screen and (max-width:530px) {
    .search-input {
        width: 300px;
        padding: 12px 100px 12px 20px;
        border: 0;
        outline: 0;

        font-size: 14px;

        background: #2d2f30;
        color: #f2f2f2;
    }
}
/* Search results */
.character-container {
    max-height: 283px;

    border-radius: 0 0 25px 25px;

    border-top: 2px solid #110b0b;
    background-color: #2d2f30;
    color: #f2f2f2;

    overflow: hidden;
}

.character-container>h3 {
    color: #949494;
    padding: 5px 24px 10px 24px;
}

/* Search result characters */
.character-details {
    padding: 5px 24px 10px 24px;
    cursor: pointer;
}

.character-details:hover {
    background-color: #414344;
}

.character-details__name-gender {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.character-details-gender,
.character-details>span {
    text-transform: capitalize;
    color: #949494;
}

.active {
    background-color: #414344;
}
.not-found {
    color: #ecd014;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins',
        sans-serif;
}

.app {
    min-height: 100vh;
    background: #110b0b;

    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}
